import { Component, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';

import { ConfigService } from '../config/config.service';
import { PrinterStatus } from '../model';
import { EnclosureStatus } from '../model/octoprint';
import { PrinterService } from '../services/printer/printer.service';
import { SocketService } from '../services/socket/socket.service';
import { EnclosureService } from '../services/enclosure/enclosure.service'; // Pour rcuprer et enregistrer les valeurs de temprature de l'enceinte et de l'emplacement des filaments. Aussi pour communiquer avec le plugin Enclosure

@Component({
  selector: 'app-printer-status',
  templateUrl: './printer-status.component.html',
  styleUrls: ['./printer-status.component.scss'],
})
export class PrinterStatusComponent implements OnInit, OnDestroy {
  private subscriptions: Subscription = new Subscription(); // Pour ajouter des lectures automatiques
  public printerStatus: PrinterStatus;  // Contient les informations de l'imprimante

  public fanSpeed: number; // Vitesse du ventilateur prs de la buse de l'imprimante

  public hotendTarget: number;  // Temprature cible qui va tre chang pour la buse de l'imprimante
  public heatbedTarget: number;
  public fanTarget: number;
  public enclosureTarget: number; // Variable qui contient la temprature cible qui va tre chang pour le boitier
  public storageTarget: number; // Variable qui contient la temprature cible qui va tre chang de l'emplacement des filaments

  public QuickControlView = QuickControlView;
  public view = QuickControlView.NONE;

  public valeurChangePourDefaut = -999; // Valeur pour changer entre le minimum et le maximum

  public enclosureStatus: EnclosureStatus;  // Objet qui contient les informations de l'enceinte au complet

  public constructor(
    private printerService: PrinterService,
    private socketService: SocketService,
    private enclosureService: EnclosureService,
    private configService: ConfigService,
  ) {
    this.hotendTarget = this.configService.getDefaultHotendTemperature();
    this.heatbedTarget = this.configService.getDefaultHeatbedTemperature();
    this.fanTarget = this.configService.getDefaultFanSpeed();
    this.enclosureTarget = this.configService.getDefaultEnclosureTemperature();
    this.storageTarget = this.configService.getDefaultStorageTemperature();
  }

  public ngOnInit(): void {
    this.subscriptions.add(
      this.socketService.getPrinterStatusSubscribable().subscribe((status: PrinterStatus): void => {
        this.printerStatus = status;
      }),
    );
    this.subscriptions.add( // Ajoute une lecture automatique
      this.enclosureService.getEnclosureStatusSubscribable().subscribe(status => {this.enclosureStatus = status}) // Lecture des informations de l'enceinte
    );
  }

  public ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }

  public showQuickControlHotend(): void {
    this.view = QuickControlView.HOTEND;
    this.showQuickControl();
  }

  public showQuickControlHeatbed(): void {
    this.view = QuickControlView.HEATBED;
    this.showQuickControl();
  }

  public showQuickControlFan(): void {
    this.view = QuickControlView.FAN;
    this.showQuickControl();
  }

  /*
   * @brief: Mthode qui fait aparaitre le menu de controle pour la temprature du boitier
   * @param: aucun
   */
  public showQuickControlEnclosure(): void {
    this.view = QuickControlView.ENCLOSURE; // Rgle l'affichage pour celui de l'enceinte
    this.showQuickControl();  // Affiche le menu de paramtrage
  }

  /*
   * @brief: Mthode qui fait aparaitre le menu de contrle pour la temprature de l'emplacement des filaments
   * @aucun: aucun
   */
  public showQuickControlStorage(): void {
    this.view = QuickControlView.STORAGE; // Rgle l'affichage pour celui de l'espace des filaments
    this.showQuickControl();  // Affiche le menu de paramtrage
  }

  private showQuickControl(): void {
    setTimeout((): void => {
      const controlViewDOM = document.getElementById('quickControl');
      controlViewDOM.style.opacity = '1';
    }, 50);
  }

  public hideQuickControl(): void {
    const controlViewDOM = document.getElementById('quickControl');
    controlViewDOM.style.opacity = '0';
    setTimeout((): void => {
      this.view = QuickControlView.NONE;
    }, 500);
  }

  public stopPropagation(event: Event): void {
    event.stopPropagation();
  }

  /*
   * @brief: Mthode qui change la temprature cible lorsque les boutons de changements de valeurs sont appuys 
   * @param value: Contient la temprature qui sera additione  celle cible. Elle est dtermine par la valeur affiche par le bouton appuy
   */
  public quickControlChangeValue(value: number): void {
    switch (this.view) {
      case QuickControlView.HOTEND:
        this.changeTemperatureHotend(value);
        break;
      case QuickControlView.HEATBED:
        this.changeTemperatureHeatbed(value);
        break;
      case QuickControlView.FAN:
        this.changeSpeedFan(value);
        break;
      case QuickControlView.ENCLOSURE:  // Si l'affichage de controle est celui pour le boitier
        this.changeTemperatureEnclosure(value); // Appel la mthode pour changer la temprature cible du boitier en additionnant avec la valeur passe en paramtre
        break;
      case QuickControlView.STORAGE:  // Si l'affichage de controle est celui pour l'emplacement des filaments
        this.changeTemperatureStorage(value); // Appel la mthode pour changer la temprature cible de l'emplacement des filaments en additionnant avec la valeur passe en paramtre
        break;
    }
  }

  /*
   * @brief: Mthode qui change la temprature appliqu par celle cible lorsque l'utilisateur appuie sur le bouton 'Set'
   * @param: aucun
   */
  public quickControlSetValue(): void {
    switch (this.view) {
      case QuickControlView.HOTEND:
        this.setTemperatureHotend();
        break;
      case QuickControlView.HEATBED:
        this.setTemperatureHeatbed();
        break;
      case QuickControlView.FAN:
        this.setFanSpeed();
        break;
      case QuickControlView.ENCLOSURE:  // Si l'affichage de controle est celui pour le boitier
        this.setTemperatureEnclosure(); // Appel la mthode pour changer la temprature applique par celle cible pour le boitier
        break;
      case QuickControlView.STORAGE:  // Si l'affichage de controle est celui pour l'emplacement des filaments
        this.setTemperatureStorage(); // Appel la mthode pour changer la temprature applique par celle cible pour l'emplacement des filaments
         break;
    }
  }

  private changeTemperatureHotend(value: number): void {
    this.hotendTarget += value;
    if (this.hotendTarget < -999) {
      this.hotendTarget = this.configService.getDefaultHotendTemperature();
    } else if (this.hotendTarget < 0) {
      this.hotendTarget = 0;
    } else if (this.hotendTarget > 999) {
      this.hotendTarget = 999;
    }
  }

  private changeTemperatureHeatbed(value: number): void {
    this.heatbedTarget += value;
    if (this.heatbedTarget < -999) {
      this.heatbedTarget = this.configService.getDefaultHeatbedTemperature();
    } else if (this.heatbedTarget < 0) {
      this.heatbedTarget = 0;
    } else if (this.heatbedTarget > 999) {
      this.heatbedTarget = 999;
    }
  }

  private changeSpeedFan(value: number): void {
    this.fanTarget += value;
    if (this.fanTarget < -999) {
      this.fanTarget = this.configService.getDefaultFanSpeed();
    } else if (this.fanTarget < 0) {
      this.fanTarget = 0;
    } else if (this.fanTarget > 100) {
      this.fanTarget = 100;
    }
  }

  /*
   * @brief: Mthode qui change la temprature cible pour le boitier
   * @param value: Contient la valeur qui sera additionn avec celle cible
   */
  private changeTemperatureEnclosure(value: number): void {
    this.enclosureTarget += value;  // Additionne la valeur  la temprature cible
    if (this.enclosureTarget < this.valeurChangePourDefaut) { // Si la temprature cible est plus petite que celle pour indiquer de changer pour la temprature maximum
      this.enclosureTarget = this.configService.getDefaultEnclosureTemperature();  // Met la temprature cible gale  la temprature idal
    } else if (this.enclosureTarget < this.enclosureStatus.enclosure.temperature.min) { // Si la temprature cible est plus petite que son minimum
      this.enclosureTarget = this.enclosureStatus.enclosure.temperature.min;  // Met la temprature cible  son minimum
    } else if (this.enclosureTarget > this.enclosureStatus.enclosure.temperature.max) { // Si la temprature cible est plus grande que son maximum
      this.enclosureTarget = this.enclosureStatus.enclosure.temperature.max;  // Met la temprature cible  son maximum
    }
  }

  /*
   * @brief: Mthode qui change la temprature cible pour l'emplacement des filaments
   * @param value: Contient la valeur qui sera additionn avec celle cible
   */
  private changeTemperatureStorage(value: number): void {
    this.storageTarget += value;  // Additionne la valeur  la temprature cible
    if (this.storageTarget < this.valeurChangePourDefaut) { // Si la temprature cible est plus petite que celle pour indiquer de changer pour la temprature maximum
      this.storageTarget = this.configService.getDefaultStorageTemperature();  // Met la temprature cible gale  la temprature idal
    } else if (this.storageTarget < this.enclosureStatus.storage.temperature.min) { // Si la temprature cible est plus petite que son minimum
      this.storageTarget = this.enclosureStatus.storage.temperature.min;  // Met la temprature cible  son minimum
    } else if (this.storageTarget > this.enclosureStatus.storage.temperature.max) { // Si la temprature cible est plus grande que son maximum
      this.storageTarget = this.enclosureStatus.storage.temperature.max;  // Met la temprature cible gale  la temprature maximum
    }
  }

  private setTemperatureHotend(): void {
    this.printerService.setTemperatureHotend(this.hotendTarget);
    this.hideQuickControl();
  }

  private setTemperatureHeatbed(): void {
    this.printerService.setTemperatureBed(this.heatbedTarget);
    this.hideQuickControl();
  }

  private setFanSpeed(): void {
    this.printerService.setFanSpeed(this.fanTarget);
    this.hideQuickControl();
  }

  /*
   * @brief: Mthode qui met le temprature applique du botier gale  celle cible
   * @param: aucun
   */
  private setTemperatureEnclosure(): void {
    this.enclosureService.setTemperatureHeater(this.configService.getEnclosureTemperatureSensorName(),this.enclosureTarget);  // Envoie la temprature de l'enceinte souhait au plugin Enclosure pour qu'il agit avec l'lment chauffant
    this.hideQuickControl();  // Ferme l'affichage de paramtrage de la temprature
  }

  /*
   * @brief: Mthode qui met la temprature applique de l'emplacement des filaments gale  celle cible
   * @param: aucun
   */
  private setTemperatureStorage(): void {
    this.enclosureService.setTemperatureHeater(this.configService.getStorageTemperatureSensorName(),this.storageTarget);  // Envoie la temprature de l'emplacement des filament souhait au plugin Enclosure pour qu'il agit avec l'lment chauffant
    this.hideQuickControl();  // Ferme l'affichage de paramtrage de la temprature
  }
}

enum QuickControlView {
  NONE,
  HOTEND,
  HEATBED,
  FAN,
  ENCLOSURE,
  STORAGE
}